import template from './fast-order-index.html.twig';
import './fast-order-index.scss';

const { Component } = Shopware;

Component.register('fast-order-index', {
    template: `
        <div class="fast-order-index">
            <h1>Fast Orders</h1>
            <p>Manage all fast orders here.</p>
            
            <button class="sw-button sw-button--primary" @click="loadOrders">
                Load Fast Orders
            </button>
            
            <div v-if="loading" class="loading">Loading...</div>
            
            <table v-if="!loading && orders.length" class="sw-data-grid">
                <thead>
                    <tr>
                        <th>Order ID</th>
                        <th>Product</th>
                        <th>Quantity</th>
                        <th>Date</th>
                        <th>Comment</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(order, index) in orders" :key="order.id">
                        <td>{{ order.sessionId }}</td>
                        <td>{{ order.productNumber }}</td>
                        <td>{{ order.quantity }}</td>
                        <td>{{ order.createdAt }}</td>
                        <td>
                            <textarea v-model="order.comment"></textarea>
                        </td>
                        <td>
                             <button @click="saveComment(index)">Save Comment</button>
                        </td>
                    </tr>
                </tbody>
            </table>
            
            <div v-if="!loading && !orders.length" class="no-orders">
                <p>No fast orders found.</p>
            </div>
        </div>
    `,

    data() {
        return {
            loading: false,
            orders: [],
        };
    },

    methods: {
        async loadOrders() {
            this.loading = true;
            try {
                // Simulierter API-Request
                const response = await fetch('/api/fast-orders');
                const data = await response.json();
                this.orders = data;
            } catch (error) {
                console.error('Failed to load orders:', error);
            } finally {
                this.loading = false;
            }
        },
        fetchOrders() {
            // Simulate fetching data from an API
            this.orders = [
            {
                createdAt: "2025-01-01 12:34:56",
                sessionId: "abc123",
                productNumber: "12345",
                quantity: 2,
                comment: ""
            },
            {
                createdAt: "2025-01-01 12:34:56",
                sessionId: "abc123",
                productNumber: "67890",
                quantity: 5,
                comment: ""
            },

            {
                createdAt: "2025-01-02 14:22:10",
                sessionId: "def456",
                productNumber: "54321", 
                quantity: 1,
                comment: "Urgent order"
            }
            ]
        },
        saveComment(index) {
            const order = this.orders[index];
            // Simulate saving the comment via an API call
            console.log(`Saving comment for session ${order.sessionId}:`, order.comment);
            alert("Comment saved!");
        }
        },
        mounted() {
        this.fetchOrders();
        } 
    
});

  